import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-stand-core',
  templateUrl: './stand-core.page.html',
  styleUrls: ['./stand-core.page.scss'],
})
export class StandCorePage implements OnInit {

  featuresImgs: any[] = [];
  responsiveOptions: any[] = [
    {
      breakpoint: '1024px',
      numVisible: 5
    },
    {
      breakpoint: '768px',
      numVisible: 3
    },
    {
      breakpoint: '560px',
      numVisible: 1
    }
  ];

  showDialog: boolean = false;

  constructor() { }

  ngOnInit() {
    for (let index = 8; index <= 18; index++) {
      const imgNumber = index < 10 ? `0${index}` : index;

      const src = `assets/imgs/solucoes/stand-core/stand-core-web_0${imgNumber}.png`;
      const feature = `stand-core.feature-outros-${index}`;

      const obj = {
        src,
        feature,
      }
      this.featuresImgs.push(obj);
    }
  }

  doContato() {
    this.showDialog = true;
  }

  close() {
    this.showDialog = false;
  }

}
